'use client'

import Link from 'next/link'
import { ReactNode } from 'react'

interface CardProps {
  title: string
  description?: string
  href?: string
  onClick?: () => void
  children?: ReactNode
  className?: string
  emoji?: string
}

const Card = ({ 
  title, 
  description, 
  href, 
  onClick, 
  children, 
  className = '',
  emoji 
}: CardProps) => {
  const cardContent = (
    <div 
      className={`
        bg-white rounded-2xl shadow-lg hover:shadow-xl transition-all duration-300 
        hover:scale-105 cursor-pointer border-2 border-transparent hover:border-purple-300
        p-6 text-center min-h-[200px] flex flex-col justify-center items-center
        ${className}
      `}
      onClick={onClick}
    >
      {emoji && (
        <div className="text-6xl mb-4">
          {emoji}
        </div>
      )}
      
      <h3 className="text-2xl font-bold text-gray-800 mb-3">
        {title}
      </h3>
      
      {description && (
        <p className="text-gray-600 text-lg leading-relaxed">
          {description}
        </p>
      )}
      
      {children}
    </div>
  )

  if (href) {
    return (
      <Link href={href} className="block">
        {cardContent}
      </Link>
    )
  }

  return cardContent
}

export default Card
